

import React, { useState } from "react";

// react router
import { 
  HashRouter as Router, 
  Switch, 
  Route, 
  Link } from "react-router-dom";

// to enable linked routes to always go to the top of the page
import ScrollToTop from './components/ScrollToTop';

// header
import Header from './components/Header';
import {ReactComponent as Logo} from './images/logo.svg';

// footer
import Footer from './components/Footer';
import {ReactComponent as About} from './images/about.svg';

// components to add styles
import DividerStyled from './components/DividerStyled';
import UnorderedListStyled from './components/UnorderedListStyled';
import ListItemStyled from './components/ListItemStyled';
import ButtonStyled from './components/ButtonStyled';

// components for each individual route
import Home from './components/Home';
import RawFood from './components/RawFood';
import CookedFood from './components/CookedFood';
import MentalHealth from './components/MentalHealth';
import SpiritualHealth from './components/SpiritualHealth';
import Reference from './components/Reference';
import Summary from './components/Summary';

// global CSS
import { createGlobalStyle } from 'styled-components';
import background from './images/background.png';
const GlobalStyle = createGlobalStyle`
  body {
    margin: 0;
    padding: 0;
    background-image: url(${background});
    font-family: Open-Sans, Helvetica, Sans-Serif;
  }
`;

// main
const App = () => {
  // encourageMessage
  const [ servings, setServings ] = useState({
    // FDA recommends 4 servings daily
    defaultFruits: 3,
    fruits: 0,
    // FDA recommends 5 servings daily
    defaultVeggies: 4,
    veggies: 0,
    carbohydrates: 0,
    proteins: 0
  })

  const randomizeDefaultFruitServing = () => (
    // new default range = (2, 6)
    setServings( servings => ({
      ...servings,
      defaultFruits: 2 + Math.trunc((Math.random() * 5))
    }))
  )

  const addFruitServing1 = () => (
    setServings( servings => ({
      ...servings,
      fruits: servings.fruits + 1
    }))
  )

  const addFruitServing2 = () => (
    setServings( servings => ({
      ...servings,
      fruits: servings.fruits + 2
    }))
  )

  const resetFruitServing = () => (
    setServings( servings => ({
      ...servings,
      fruits: 0
    }))
  )

  const randomizeDefaultVeggieServing = () => (
    // new default range = (3, 7)
    setServings( servings => ({
      ...servings,
      defaultVeggies: 3 + Math.trunc((Math.random() * 5))
    }))
  )

  const addVeggieServing1 = () => (
    setServings( servings => ({
      ...servings,
      veggies: servings.veggies + 1
    }))
  )

  const addVeggieServing2 = () => (
    setServings( servings => ({
      ...servings,
      veggies: servings.veggies + 2
    }))
  )

  const resetVeggieServing = () => (
    setServings( servings => ({
      ...servings,
      veggies: 0
    }))
  )

  return (
    <>
      <GlobalStyle />
      <Router>
        {/* to enable linked routes to always go to the top of the page */}
        <ScrollToTop />

          {/* upper-div: header */}
          {/* header */}
          <DividerStyled>
            <Header>
              <Logo />
            </Header>
          </DividerStyled>
          {/* header */}
          {/* upper-div: header */}


        {/* middle-div: sidebar (left) & main display (right) */}
        <DividerStyled comparison>

          {/* side bar */}
          <DividerStyled>
            
            <UnorderedListStyled sidebar>

              <ListItemStyled>
                <Link to="/">
                  <ButtonStyled silver>
                    Home
                  </ButtonStyled>
                </Link>
              </ListItemStyled>

              <ListItemStyled>
                <Link to="/raw-food">
                  <ButtonStyled>
                    Raw Food
                  </ButtonStyled>
                </Link>
              </ListItemStyled>

              <ListItemStyled>
                <Link to="/cooked-food">
                  <ButtonStyled silver>
                  Cooked Food
                  </ButtonStyled>
                </Link>
              </ListItemStyled>

              <ListItemStyled>
                <Link to="/mental-health">
                  <ButtonStyled>
                    Mental Health
                  </ButtonStyled>
                </Link>
              </ListItemStyled>

              <ListItemStyled>
                <Link to="/spiritual-health">
                  <ButtonStyled silver>
                  Spiritual Health
                  </ButtonStyled>
                </Link>
              </ListItemStyled>

              <ListItemStyled>
                <Link to="/reference">
                  <ButtonStyled>
                    Reference
                  </ButtonStyled>
                </Link>
              </ListItemStyled>

              <ListItemStyled>
                <Link to="/summary">
                  <ButtonStyled silver>
                    Summary
                  </ButtonStyled>
                </Link>
              </ListItemStyled>
              
            </UnorderedListStyled>

          </DividerStyled>         
          {/* side bar */}

          {/* main display */}
          <DividerStyled>
            <Switch>

              <Route exact path='/'>
                <Home 
                  myServings={ servings }
                  />
              </Route>          

              <Route exact path='/raw-food'>
                <RawFood 
                  myServings={ servings }
                  myAddFruitServing1={ addFruitServing1 }
                  myAddFruitServing2={ addFruitServing2 }
                  myResetFruitServing={ resetFruitServing }                
                  myAddVeggieServing1={ addVeggieServing1 }
                  myAddVeggieServing2={ addVeggieServing2 }
                  myResetVeggieServing={ resetVeggieServing }                
                  myRandomizeDefaultFruitServing={ randomizeDefaultFruitServing }
                  myRandomizeDefaultVeggieServing={ randomizeDefaultVeggieServing }
                />
              </Route>          

              <Route exact path='/cooked-food'>
                <CookedFood />
              </Route>          

              <Route exact path='/mental-health'>
                <MentalHealth />
              </Route>          

              <Route exact path='/spiritual-health'>
                <SpiritualHealth />
              </Route>          

              <Route exact path='/reference'>
                <Reference />
              </Route>          

              <Route exact path='/summary'>
                <Summary 
                  myServings={ servings }                
                />
              </Route>          

            </Switch>

          </DividerStyled>
          {/* main display */}

        </DividerStyled>
        {/* middle-div: sidebar (left) & main display (right) */}
          
        {/* lower-div: footer */}
        {/* footer */}
        <DividerStyled>
          <Footer>
            <About />
          </Footer>
        </DividerStyled>
        {/* footer */}
        {/* lower-div: footer */}

      </Router>
    </>
  );
}
export default App;